import React, { useState, useEffect } from "react";
import { Layout } from "../../components/Layout";
import axios from "axios";
import { useNavigate, useParams } from "react-router-dom";
import { showLoading, hideLoading } from "../../redux/features/alertSlice";
import { useDispatch } from "react-redux";
import { toast } from "react-toastify";

const SingleClub = () => {
  const params = useParams();
  const dispatch = useDispatch();
  const [club, setClub] = useState({
    clubId: "",
    name: "",
    clubType: "",
    img: [],
    clubInfo: "",
    resources: "",
    applicationForm: "",
    date: "",
  });

  const getClubInfo = async () => {
    try {
      const res = await axios.post(
        "/api/v1/admin/get-club-info",
        { clubId: params.id },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
        setClub(res.data.data);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setClub({
      ...club,
      [name]: value,
    });
  };

  const handleUpdate = async () => {
    try {
      dispatch(showLoading());
      const res = await axios.post(
        "/api/v1/admin/update-club",
        { ...club },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      dispatch(hideLoading());
      if (res.data.success) {
        toast.success(res.data.message);
      } else {
        toast.error("Something went wrong");
      }
    } catch (error) {
      dispatch(hideLoading());
      toast.error("Error updating profile");
    }
  };

  useEffect(() => {
    getClubInfo();
  }, [params.id]);

  return (
    <Layout>
      <div className="min-h-screen bg-gray-900 text-white p-6">
        <h1 className="text-3xl font-bold mb-4">Club Details</h1>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {club.img.map((image, index) => (
            <div key={index} className="relative w-9/12 h-100 mb-4">
              <img
                src={image}
                alt={`Club ${index}`}
                className="w-full h-full object-cover rounded-md shadow-lg"
              />
            </div>
          ))}
        </div>
        <form className="space-y-4">
          <div>
            <label className="block text-sm font-medium mb-2" htmlFor="name">
              Name
            </label>
            <input
              type="text"
              id="name"
              name="name"
              value={club.name}
              onChange={handleChange}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-md focus:outline-none focus:ring-2 focus:ring-green-500"
            />
          </div>
          <div>
            <label
              className="block text-sm font-medium mb-2"
              htmlFor="clubType"
            >
              Club Type
            </label>
            <input
              type="text"
              id="clubType"
              name="clubType"
              value={club.clubType}
              onChange={handleChange}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-md focus:outline-none focus:ring-2 focus:ring-green-500"
            />
          </div>
          <div>
            <label
              className="block text-sm font-medium mb-2"
              htmlFor="clubInfo"
            >
              Club Info
            </label>
            <textarea
              id="clubInfo"
              name="clubInfo"
              value={club.clubInfo}
              onChange={handleChange}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-md focus:outline-none focus:ring-2 focus:ring-green-500"
              rows="4"
            />
          </div>
          <div>
            <label
              className="block text-sm font-medium mb-2"
              htmlFor="resources"
            >
              Resources
            </label>
            <textarea
              id="resources"
              name="resources"
              value={club.resources}
              onChange={handleChange}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-md focus:outline-none focus:ring-2 focus:ring-green-500"
              rows="4"
            />
          </div>
          <div>
            <label
              className="block text-sm font-medium mb-2"
              htmlFor="applicationForm"
            >
              Application Form
            </label>
            <textarea
              id="applicationForm"
              name="applicationForm"
              value={club.applicationForm}
              onChange={handleChange}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-md focus:outline-none focus:ring-2 focus:ring-green-500"
              rows="4"
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-2" htmlFor="date">
              Date of Establishment
            </label>
            <input
              type="date"
              id="date"
              name="date"
              value={club.date}
              onChange={handleChange}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-md focus:outline-none focus:ring-2 focus:ring-green-500"
            />
          </div>
          <button
            type="button"
            onClick={handleUpdate}
            className="w-full bg-green-500 text-white py-2 px-4 rounded-md hover:bg-green-600 transition-colors duration-200"
          >
            Update
          </button>
        </form>
      </div>
    </Layout>
  );
};

export default SingleClub;
